.projects-container {
    display: flex;
    gap: 30px;
    justify-content: center;
    margin: 10px auto;
    text-align: justify;
    padding-top: 0px;
    margin-top: 80px;
  }
  
.project-card {
    background-color: #171B21;
    color: #B9B9B9;
    border-radius: 3px;
    padding: 20px;
    width: 360px;
    height: 350px; 
    display: flex;
    flex-direction: column;
    justify-content: space-between; 
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease;
    margin-bottom: 0px;
    padding-bottom: 0px;
}
  
.project-card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}
  
.project-card-title {
    font-size: 25px;
    font-weight: bold;
    color: #45A29E;
    margin-bottom: 10px;
}
  
.project-card-description {
    font-size: 16px;
    line-height: 1.5;
    flex: 1; 
    margin-bottom: 15px;
}
  
.project-card-technologies {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    padding-bottom: 9px;
}
  
.project-card-technologies span {
    background-color: transparent;
    color: #B9B9B9;
    padding: 5px 2px;
    border-radius: 5px;
    font-size: 13px;
}

.section-title {
    font-size: 40px;
    font-weight: bold;
    color: white;
    position: relative;
    display: inline-block; 
    padding: 12px 20px; 
}

.section-title::before,
.section-title::after {
    content: "";
    position: absolute;
    top: 50%; 
    width: 300px; 
    height: 1px; 
    background-color: white; 
    transform: translateY(-50%); 
}

.section-title::before {
    left: -330px; 
}

.section-title::after {
    right: -330px;
}

.technologies {
    display: flex; 
    justify-content: start; 
    gap: 150px; 
    max-width: 800px; 
    margin: 30px 50px; 
}

.technologies ul {
    list-style: none; 
    padding: 0; 
    margin: 0; 
}

.technologies li {
    font-size: 18px; 
    line-height: 2; 
    color: white; 
    position: relative; 
}

.technologies li::before {
    content: 'â–¹'; 
    position: absolute;
    left: -20px; 
    color: #45A29E; 
    font-size: 18px; 
}
  
.github-icon img {
    width: 30px;
    height: 30px;
    cursor: pointer;
    transition: transform 0.3s ease, filter 0.3s ease;
}
  
.github-icon img:hover {
    transform: scale(1.2); 
    filter: brightness(1.5);
}
  
.github-icon img:active {
    transform: scale(1); 
    filter: brightness(1); 
}